<template>
  <div data-cursor>
    <div class="cur1">
      <span class="see">Voir</span>
    </div>
    <div class="cur2"></div>
  </div>
</template>

<script>
import $ from "jquery";
import gsap from "gsap-3";

var $g = gsap;

export default {
  name: "AppCursor",
  mounted() {
    this.init();
  },
  methods: {
    init() {
      var $cursor = {
        cur: $("[data-cursor]"),
        cur1: $("[data-cursor] .cur1"),
        cur2: $("[data-cursor] .cur2"),
        classCursor: function() {
          var t = this;
          $("[data-cc]").each(function() {
            var $t = $(this);
            var c = $t.data("cc");
            $t.on({
              mouseover: function() {
                $("body").addClass(c);
              },
              mouseleave: function() {
                $("body").removeClass(c);
              },
            });
          });
        },
        init: function() {
          var t = this;
          t.classCursor();
          $(document).on({
            mousemove: function(e) {
              var cx = e.pageX;
              var cy = e.pageY;
              $g.to(t.cur1, 0, { left: cx, top: cy });
              $g.to(t.cur2, 0.15, { left: cx, top: cy });
            },
          });
          $(
            ".line-ec .lp > *, form fieldset, form label, input, textarea, a, button, [data-nav], [data-link], [data-hover], [data-mail], [data-part]"
          ).each(function() {
            $(this).on({
              mouseenter: function() {
                $("body").addClass("hover");
              },
              mouseleave: function() {
                $("body").removeClass("hover");
              },
            });
          });
        },
      };

      $cursor.init();
    },
  },
};
</script>

<style lang="scss" scoped>
